<template>
  <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" @close="hide" width="600px">
    <el-form :model="submitData" ref="form" :rules="formValidate" label-position="left" label-width="100px">
      <el-row :gutter="20">
        <el-col :span="22">
          <el-form-item label="广告样式:">
            <el-select style="width:100%" v-model="submitData.type" placeholder="请选择广告样式">
              <el-option v-for="item in typeList" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="22">
          <el-form-item label="广告名称:" prop="name">
            <el-input v-model="submitData.name" placeholder="请输入广告名称"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="22">
          <el-form-item label="有效期:" prop="dateTimeRange">
            <el-date-picker v-model="submitData.dateTimeRange" type="datetimerange" value-format="yyyy-MM-dd HH:mm:ss" range-separator="-" start-placeholder="开始时间" end-placeholder="结束时间" align="right">
            </el-date-picker>
          </el-form-item>
        </el-col>
      </el-row>

			<el-row :gutter="20">
        <el-col :span="22">
          <el-form-item label="广告权重:" prop="weight">
            <el-input v-model="submitData.weight" type="number" placeholder="请输入广告权重"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="22">
          <el-form-item label="广告配图:" prop="coverImage">
            <div class="img-tip" style="color: red">* 建议宽高比690:290</div>
            <el-upload class="avatar-uploader" :action="ajaxUrl.baseUrl + ajaxUrl.uploadArticleImage" :show-file-list="false" :on-success="handleAvatarSuccess">
              <img v-if="submitData.coverImage" :src="submitData.coverImage" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="22">
          <el-form-item label="超链接:" prop="url">
            <el-input v-model="submitData.url" placeholder="请输入超链接地址"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

    </el-form>
    <div slot="footer" align="center" class="dialog-footer">
      <el-button type="primary" @click="hide">取 消</el-button>
      <el-button type="success" @click="saveOrUpdate">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { ajaxPostCommon, ajaxUploadCommon } from "@/api/common";
import * as ajaxUrl from "@/api/url";
import moment from "moment";

export default {
  data() {
    return {
      ajaxUrl,
      dialogTitle: "",
      submitData: {
        type: "00",
        coverImage: "",
        dateTimeRange: []
      },
      formValidate: {
        name: [
          { required: true, message: "请输入广告名称", trigger: "blur" },
          { max: 40, message: "最多40个字", trigger: "blur" }
        ],
        dateTimeRange: [
          { required: true, message: "请选择有效期", trigger: "blur" }
        ],
        coverImage: [
          { required: true, message: "请选择广告图", trigger: "blur" }
        ],
				url: [{ required: true, message: "请填写超链接", trigger: "blur" }],
				weight: [{ required: true, message: "请输入广告权重", trigger: "blur" }]
      },
      typeList: [{ label: "信息流广告", value: "00" }],
      dialogVisible: false
    };
  },
  methods: {
    show(data) {
      this.submitData = this.parseData(data) || this.submitData;
      this.dialogVisible = true;
    },
    parseData(data) {
      if (!data) {
        this.dialogTitle = "新建广告";
        return false;
      }
      this.dialogTitle = "编辑广告";
      const forMatter = "YYYY-MM-DD HH:mm:ss";
      const dateTimeRange = [
        moment(data.gmtValidityStart).format(forMatter),
        moment(data.gmtValidityEnd).format(forMatter)
      ];
      return { ...data, dateTimeRange };
    },
    hide() {
      this.submitData = {};
      this.$refs.form.resetFields();
      this.dialogVisible = false;
    },
    parseSubmitData(submitData) {
      const forMatter = "YYYY:MM:DD HH:mm:ss";
      submitData.dateTimeRange = [
        moment(submitData.gmtValidityStart).format(forMatter),
        moment(submitData.gmtValidityEnd).format(forMatter)
      ];
      return submitData;
    },
    ctrlSubmitData(submitData) {
      submitData.gmtValidityStart = submitData.dateTimeRange[0];
      submitData.gmtValidityEnd = submitData.dateTimeRange[1];
      delete submitData.dateTimeRange;
      return submitData;
    },
    validateForm() {
      let validatePass = false;
      this.$refs.form.validate(pass => {
        if (pass) validatePass = true;
      });
      return validatePass;
    },
    saveOrUpdate() {
      if (!this.validateForm()) return false;
      ajaxPostCommon(
        ajaxUrl.saveOrUpdateAd,
        this.ctrlSubmitData(this.submitData)
      ).then(res => {
        if (res.returnCode === "200") {
          this.hide();
          this.$emit("saveOrUpdate");
        } else {
          this.$message({
            message: res.returnMsg,
            type: "error"
          });
        }
      });
    },
    handleAvatarSuccess(res, file) {
      this.submitData.coverImage = res.result;
    }
  }
};
</script>

<style scoped>
.el-date-editor {
	width: 100%;
}
</style>



