<template>
  <za-page-content header="广告管理">
    <za-table ref="table" :data="list" @action-change="changePage" highlight-current-row :total="pageConfig.total" @selection-change="handleSelectionChange">
      <div style="padding-bottom: 0px;" slot="searchBar">
        <el-form :model="filters" ref="filtersForm">
          <el-form-item>
            <el-col :span="8">
              <el-col :span="6">状态:</el-col>
              <el-col :span="16">
                <el-select style="width:100%" v-model="filters.status" placeholder="请选择">
                  <el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
            </el-col>

            <el-col :span="16">
              <el-col :span="3">有效期:</el-col>
              <el-date-picker v-model="filters.dateTimeRange" type="datetimerange" value-format="yyyy-MM-dd HH:mm:ss" start-placeholder="开始时间" end-placeholder="结束时间">
              </el-date-picker>
            </el-col>
          </el-form-item>
        </el-form>
      </div>

      <el-row slot="searchBar" style="margin-top: 30px">
        <el-button size="medium" icon="el-icon-search" @click="filter" type="primary">查询</el-button>
        <el-button size="medium" @click="addAdvertise" type="success">新建广告</el-button>
      </el-row>

      <el-table-column prop="id" label="广告ID"></el-table-column>
      <el-table-column prop="type" label="广告样式">
        <template slot-scope="scope">
          信息流广告
        </template>
      </el-table-column>
      <el-table-column prop="name" label="广告名称"></el-table-column>
      <el-table-column prop="gmtValidityStart" label="开始时间" :formatter="formatterDate"></el-table-column>
      <el-table-column prop="gmtValidityEnd" label="结束时间" :formatter="formatterDate"></el-table-column>
      <el-table-column prop="weight" label="权重"></el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          {{scope.row.status === "01" ? "下线" : "上线"}}
        </template>
      </el-table-column>

      <el-table-column label="操作" width="240">
        <template slot-scope="scope">
          <el-button v-if="scope.row.status === '01'" size="mini" type="success" @click="onlineOrOffline(scope.row.id, '02')">上线</el-button>
          <el-button v-if="scope.row.status === '02'" size="mini" type="danger" @click="onlineOrOffline(scope.row.id, '01')">下线</el-button>
          <el-button size="mini" type="primary" @click="editAd(scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="deleteAd(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </za-table>
    <add-or-edit-dialog ref="addOrEditDialog" @saveOrUpdate="saveOrUpdate"></add-or-edit-dialog>
  </za-page-content>
</template>

<script>
import { ajaxPostCommon, ajaxUploadCommon } from "@/api/common";
import * as ajaxUrl from "@/api/url";
import AddOrEditDialog from "./components/AddOrEdit.vue";
import formatterDate from "@/filters/date/index";

export default {
  components: {
    AddOrEditDialog
  },
  data() {
    return {
      filters: {
        status: ""
      },
      status: [
        { label: "全部", value: "" },
        { label: "下线", value: "01" },
        { label: "上线", value: "02" }
      ],
      list: [],
      selectionRowData: [],
      pageConfig: {
        pageNum: 1,
        pageSize: 10,
        total: 1
      }
    };
  },

  created() {
    this.initData();
  },

  methods: {
    formatterDate,
    initData() {
      ajaxPostCommon(ajaxUrl.advertisementList, {
        ...this.pageConfig,
        ...this.filters
      }).then(data => {
        if (data.returnCode === "200") {
          this.list = data.result.list;
          this.pageConfig.total = data.result.total;
        }
      });
    },

    filter() {
      const gmtValidityStart = !!this.filters.dateTimeRange
        ? this.filters.dateTimeRange[0]
        : "";
      const gmtValidityEnd = !!this.filters.dateTimeRange
        ? this.filters.dateTimeRange[1]
        : "";
      this.pageConfig = {
        ...this.pageConfig,
        ...this.filters,
        gmtValidityStart,
        gmtValidityEnd,
        pageNum: 1
      };
      if (!this.$refs.table.resetCurrentPage()) {
        this.initData();
      }
    },

    changePage(params) {
      let pageParams = {
        pageNum: params.page,
        pageSize: params.rows || this.pageConfig.pageSize
      };
      this.pageConfig = { ...this.pageConfig, ...pageParams };
      this.initData();
    },

    addAdvertise() {
      this.$refs.addOrEditDialog.show();
    },

    saveOrUpdate() {
      this.$message({
        message: "成功!",
        type: "success"
      });
      this.initData();
    },

    handleSelectionChange(selection) {
      this.selectionRowData = selection;
    },

    formatterDate(r, c, v) {
      return formatterDate(v) || "无";
    },

    onlineOrOffline(id, status) {
      const data = { id, status };
      ajaxPostCommon(ajaxUrl.onlineOrOfflineAd, data).then(res => {
        if (res.returnCode === "200") {
          this.initData();
        } else {
          this.$message({
            message: res.returnMsg,
            type: "error"
          });
        }
      });
    },

    editAd(row) {
      this.$refs.addOrEditDialog.show(row);
    },

    deleteAd(id) {
      this.$confirm("确定删除广告么?", "提示", {})
        .then(() => {
          ajaxPostCommon(ajaxUrl.deleteAd, { id }).then(res => {
            if (res.returnCode === "200") {
              this.initData();
              this.$message({
                message: "删除成功",
                type: "success"
              });
            } else {
              this.$message({
                message: res.returnMsg,
                type: "error"
              });
            }
          });
        })
        .catch(() => {});
    }
  }
};
</script>




